<script>
    import { onMount } from 'svelte';
    import {Select, Field} from 'svelma'
    export let display = 'none';
	let options = [
        { id: 2, text: 'Edit Call Center' },
		{ id: 1, text: 'Transmit File' }
	];
    export let selected;
    export let list;
    export let status;

    function OnBlurSelect(){
        display = 'none';
    }
 
</script>

<div style='display:{display};z-index: 30;position: absolute;top:5px;height: 40px;'>
    <Select placeholder="Options:" on:blur = {OnBlurSelect} bind:this={list} bind:selected={selected}>
        {#each options as opt (opt.id)}
            {#if opt.id==1 && status==='talk'}
                <option value={opt.id}>{opt.text}</option>
            {/if}
            {#if opt.id==2}
                <option value={opt.id}>{opt.text}</option>
            {/if}
        {/each}
    </Select>
</div>

