
<div style = "display:{display}; position: relative;right: -20px;" on:click={OnClick}>
    <slot></slot>
</div>
<script>
    import { onMount } from 'svelte';
    let display = "none"
    let visible = 'none';
    let flash;
    export let abonent;

    onMount(() => {

    })

    export function SetFlashVisible(state){
        display = state;
    }

    async function OnClick(){

        let iframe = document.querySelector("[src*='"+abonent+"']");
        let cb = iframe.contentWindow.document.querySelector('.callButton');
        cb.dispatchEvent(new Event('mute'));

        let promise = new Promise(function(resolve, reject) {
            if(window.operator.DC)
                window.operator.DC.SendRedirect({abonent}, resolve);
        });

        let date  = await promise;
        display = 'none';

        window.operator.SendToComponent({func:'mute'}); 
     
    }


</script>