

import {SignalingChannel} from './signalingChannel.js';
import  {RTCBase} from "./RTCBase";

import {log} from './utils'
//

export default class  RTCOperator extends RTCBase{

    constructor(trans,type, em, uid) {
        // window.operator.localSound.src = "./assets/call.mp3"
        super(trans,type,em, uid);

        this.checking_tmr;

        (async ()=>{
            this.host = (await (await fetch('/kolmi/host.json')).json());
                
            if(!this.signch) {
                this.signch = new SignalingChannel(this.host.host_ws);
            }

            this.SendCheck();
        })();
    }

    Init(cb){
        let that = this;

        this.mode = '';
        let transAr=[that.trans];
        that.main_pc = '';
        for (let i in transAr) {
            that.InitRTC(transAr[i],function () {
                cb();
            });
        }
    }


    SendOffer(key) {

        let that = this;

        try {
            // Fix up for prefixing
            window.AudioContext = window.AudioContext||window.webkitAudioContext;
            let audioCtx = new AudioContext();
            window.operator.localSoundSrc = audioCtx.createMediaElementSource(window.operator.localSound );
            window.operator.localSoundSrc.connect(audioCtx.destination);
        }
        catch(e) {
            log('Web Audio API is not supported in this browser');
        }

        that.pcPull[key].params['loc_desc'] = '';
        that.pcPull[key].params['loc_cand'] = '';

        //log('pcPull createOffer start', that);
        that.pcPull[key].con.createOffer(this.mode=
            {
                offerToReceiveAudio: 1,
                offerToReceiveVideo: 0,
                iceRestart:1
            }
        ).then(
            desc => that.pcPull[key].onCreateOfferSuccess(desc),
            that.pcPull[key].onCreateOfferError
        );

    }

    SendVideoOffer(key){
        let that = this;
        that.pcPull[key].params['loc_desc'] = '';
        that.pcPull[key].params['loc_cand'] = '';

        that.pcPull[key].con.createOffer(
            that.mode={
                offerToReceiveAudio: 1,
                offerToReceiveVideo: 1,
                iceRestart:1
            }
        ).then(
            desc => that.pcPull[key].onCreateVideoOfferSuccess(desc),
            that.pcPull[key].onCreateOfferError
        );

    }

    OnInit(){
 
        this.Init(()=> {
            if (this.pcPull[this.trans].con.signalingState !== 'closed') {
                this.GetUserMedia({audio: 1, video: 0}, (res)=> {
                        if (res) {                           
                            this.SendOffer(this.trans);                                            
                        }
                });
            }
        });
    }

    OnActive(){

        this.Init(()=> {
            if (this.pcPull[this.trans].con.signalingState !== 'closed') {

                this.GetUserMedia({audio: 1, video: 0}, (res)=> {
                    if (res) {   
                        this.SendOffer(this.trans);
                    }                    
                });
            }
        });
    }

    GetUserMedia(opts,cb){

        window.navigator.mediaDevices.getUserMedia(opts)
            .then(stream => this.gotStream(stream,cb))
            .catch(function (e) {
                if(e.name === 'NotFoundError' || e.name === 'NotReadableError') {
                    if(opts.audio)
                        alert("Something wrong with mic?");
                    if(opts.video)
                        alert("Something wrong with camera?");

                    cb(false);
                }
            });
    }

    OnCall(){

        if (this.DC) {
            //this.DC.SendDCTalk();
            clearInterval(this.DC.inter);
        }

        this.SendStatus('call');

        // $('i.video').on('click', () => {
        //     $('i.video').off('click');
        //     that.localSound.muted = 'true';
        //     $(that.localVideo).css('display', 'inline-block');
        //     if (that.DC.dc.readyState === 'open') {
        //         that.GetUserMedia({video: 1}, function () {
        //             that.SendVideoOffer(that.main_pc);
        //         });
        //     }
        // });
    }

    OnTalk(){

        if (this.DC.dc) {                
            this.DC.SendDCTalk();           
        }                        
         
        this.SendStatus('talk');
        console.log();
             
        // $('i.video').css('display', 'inline-block');

        // $('i.video').on('click', () => {
        //     $('i.video').off('click');
        //     that.localSound.muted = 'true';
        //     $(that.localVideo).css('display', 'inline-block');
        //     if (that.DC.dc.readyState === 'open') {
        //         that.GetUserMedia({video: 1}, function () {
        //             that.SendVideoOffer(that.main_pc);
        //         });
        //     }
        // });
    }

    OnMute(){

        this.RemoveTracks();
        if (this.DC)
            this.DC.SendDCHangup(() => {          

                //this.OnInit();
            });

         // $(ev.target).trigger('click');
    }

    OnInactive(){

        this.RemoveTracks();

        if (this.DC)
            this.DC.SendDCHangup(() => {         
                //this.OnInit();
                this.DC.dc.close();
                this.SendStatus('close');
            }); 
    }


    OnMessage(data) {

        let that = this;

        //log(data,that);
        if (data.func === 'call') {
   
        }

        if (data.func === 'mute') {

                this.RemoveTracks();
                // this.OnInit();
        }

        if (data.func === 'talk') {
            clearInterval(that.DC.inter);
        }

        
        if (data.func === 'redirect') {
        
        }

        if (data.func === 'video') {

        }


        if (data.desc) {
            if(that.pcPull[data.trans].con.connectionState==="failed")
                that.pcPull[data.trans].con.restartIce();

            if (that.pcPull[data.trans]) {
                that.pcPull[data.trans].params['rem_desc'] = data.desc;
                that.pcPull[data.trans].setRemoteDesc(data.desc);

                that.PlayCallCnt();
            }
        }
        if (data.cand) {
            if (that.pcPull[data.trans]) {
                if (that.pcPull[data.trans].con.signalingState === 'closed') {
                    return;
                }
                try {
                    that.pcPull[data.trans].params['rem_cand'] = data.cand;
                    if (Array.isArray(data.cand)) {
                        for (let c in data.cand) {
                            that.pcPull[data.trans].con.addIceCandidate(data.cand[c]);
                            //log(' Remote ICE candidate: \n' + (data.cand[c] ? JSON.stringify(data.cand[c]) : '(null)'), that);
                        }
                    } else {
                        that.pcPull[data.trans].con.addIceCandidate(data.cand);
                        //log(' Remote ICE candidate: \n' + (data.cand ? JSON.stringify(data.cand) : '(null)'), that);
                    }


                } catch (ex) {
                    log(ex);
                }
            }
        }
    }
}



