<script>
    import { onMount } from 'svelte';
    export let display = 'block';
    export let poster;
    export let srcObject ='';
    let lv;
    onMount(async () => {
            lv = document.getElementById('localVideo');
        });

        $: if(lv && srcObject){
            lv.srcObject = srcObject;
        }else if(lv && lv.srcObject){
            lv.srcObject.getVideoTracks().forEach(track => {
                track.stop()
                lv.srcObject.removeTrack(track);
            });
            lv.src='';
        }

</script>

<div style="position: relative;">
<video  id="localVideo" autoplay playsinline muted  poster={poster}
    style="
        display: {display};
        position: relative;
        flex:2;
        top:0px;
        float: right;
        max-height: 100px;
        z-index: 20;">
</video>
<slot></slot>
</div>