<div class:oper style="display:flex;flex-flow: row nowrap;align-items: flex-start;">
    <div class = "user_pic_div" style="position:relative" >
      <img class="user_pic is-rounded" src={src} alt="" on:click={OnClickUpload} style="border-radius: 5px;">

      {#if oper.role==="admin" && user.role!=="admin" && edited_display}
      <!-- || (user.role==='admin' && oper.dep===dep && user.email && user.email!== oper.email) -->
          <svg height="30" width="30"  on:click={OnRemoveOper} em={user.email} style="position: absolute;bottom: -15px;left: 0;">
            <glyph glyph-name="minus-circle" unicode="&#xefc0;" horiz-adv-x="50" />
            <g class="currentLayer">
              <path d="M500.2 62.5c-241.8 0-437.7 195.89999999999998-437.7 437.3 0 241.8 195.89999999999998 437.7 437.7 437.7 241.40000000000003 0 437.3-195.89999999999998 437.3-437.7 0-241.40000000000003-195.89999999999998-437.3-437.3-437.3z m301.59999999999997 466.5c-0.6999999999999318 9-2.199999999999932 19.100000000000023-4.699999999999932 30.299999999999955h-593.9000000000001c-2.499999999999943-11.199999999999932-4.299999999999926-21.699999999999932-5.0999999999999375-31.399999999999977-0.6999999999999886-9.699999999999932-1.0999999999999943-19.799999999999955-1.0999999999999943-30.299999999999955 0-9 0.4000000000000057-18 1.0999999999999943-26.700000000000045 0.700000000000017-9 2.5-19.099999999999966 5.099999999999994-30.299999999999955h593.9000000000001c2.4999999999998863 11.199999999999989 3.9999999999998863 21.599999999999966 4.699999999999818 31.399999999999977 1.1000000000000227 9.699999999999989 1.400000000000091 19.80000000000001 1.400000000000091 30.30000000000001 0.09999999999990905 9.099999999999966-0.3000000000000682 17.69999999999999-1.400000000000091 26.69999999999999z"
              transform = "scale(.03)"  style="fill:lightgrey"/>
            </g>
          </svg>
      {/if}
      <input  class="file-upload" on:change="{OnChangeFile}"
        accept="image/png, image/jpeg"
        bind:files
        id="avatar"
        name="avatar"
        type="file"  
        style="display: none"/>
    </div>

    <div style="flex:1;">
      <input type="text" class="user_name" placeholder="input user name" name={user.name.first}{user.name.last} em={user.email}  { readonly } on:change={OnNameChange} value="{titleize(user.name.first)} {titleize(user.name.last)}" style="width:80%;font-weight:bold;"/>
      <input type="text"  class="user_email" placeholder="input email address *" em={user.email}  on:change={OnEmailChange} readonly={readonlyAdm} style="width:100%;max-height: 100px;" value={user.email} />
      <textarea type="text" rows="3" class="user_desc"  placeholder="input description" desc={user.desc} em={user.email} on:change={OnDescChange} { readonly } style="width:85%;overflow:auto;max-height: 100px;resize:none" value={user.desc}/>         
    </div>

    <div style="display: flex;flex-flow: row nowrap; align-items: flex-start;flex-direction: column;">

      {#if user.email && user.email!== oper.email}
        <iframe class="user_frame" src="/kolmi/user/iframe.html?user=oper@kolmi&abonent={user.email}" scrolling="no" frameBorder="0" 
        style="position:relative;top:-10px;left:0px;width:100px;height:100px" on:display={OnDisplay} title="">
        </iframe>
      {/if}

      <Flash abonent={user.email} bind:SetFlashVisible = {SetFlashVisible}>
        <svg height="50" width="50" >
          <glyph glyph-name="flash" unicode="&#xef32;" horiz-adv-x="50" />
          <g class="currentLayer">
          <path d="M484 458.3h256.4c-121.60000000000002 160.8-242 319.99999999999994-362.4 479.2l-1.8000000000000114-1c43.5-119.39999999999998 87.10000000000002-238.70000000000005 131.40000000000003-360.5-82.90000000000003 3.7000000000000455-163.90000000000003 7.2999999999999545-248.10000000000002 11 123.39999999999998-176.3 245.39999999999998-350.4 367.4-524.5l2.300000000000068 1c-48.30000000000007 131-96.5 262.1-145.20000000000005 394.8z"
            transform = "scale(.04)"/>
          </g>
        </svg>   
      </Flash>

    </div>

  </div>

  <style>
    /* [readonly]:empty:not(:focus):before{
      content:attr(data-placeholder);
      color:lightgrey 
    } */
    input, input:hover, input:focus, input:active,textarea {
      background: transparent;
      border: 0;
      border-style: none;
      border-color: transparent;
      outline: none;
      outline-offset: 0;
      box-shadow: none;
      padding:0;
    }
  </style>

  <script>
    import { onMount } from 'svelte';
    import loadImage from "blueimp-load-image/js/load-image.js"
    import  "blueimp-load-image/js/load-image-scale.js"
    import Flash from './Flash.svelte'
    import _  from 'lodash';

    export let update;
    export let dep;
    export let user;
    export let src;
    export let oper;
    export let key;
    export let edited_display;

    let readonly = true;
    let readonlyAdm = true;
    let files;

    let SetFlashVisible;

    const titleize = s => s?s.replace(/^([a-z])/, (_, r) => r.toUpperCase()):'';

    onMount(() => {
      if ((oper.role==='admin' && (key===oper.key || oper.key==='0'))){
        readonly = false;
        if(user.email!== oper.email)
          readonlyAdm = false;
      }
    })

    function OnDisplay(ev){
        SetFlashVisible(ev.data);
    }

    function OnClickUpload(ev){
        let event =  new MouseEvent('click', {
            bubbles: true,
            cancelable: true,
            view: window,
        });
        let pic = ev.currentTarget;
        pic.nextElementSibling.dispatchEvent(event);
    }

    function OnChangeFile(e){
        try {

        loadImage(
            e.target.files[0],
            function (img, data) {
                if(img.type === "error") {
                    console.error("Error loading image ");
                } else {
                    src =  img.toDataURL();
                    window.operator.ChangeUsersList({[key]:dep});
                }
            },
            {
                orientation:true,
                maxWidth: 72,
                maxHeight: 72,
                minWidth: 50,
                minHeight: 50,
                canvas: true
            }
        );
        }catch(ex){

        }
    }


    function OnNameChange(ev){
        let em =  ev.currentTarget.attributes['em'].nodeValue; 
        let user = _.find(dep.staff,{'email':em});
        user.name.first  = ev.currentTarget.value.split(' ')[0];
        user.name.last  = ev.currentTarget.value.split(' ')[1];
        window.operator.ChangeUsersList({[key]:dep});   
    }

    function OnEmailChange(ev){
        let em =  ev.currentTarget.attributes['em'].nodeValue; 
        let user = _.find(dep.staff,{'email':em});
        user.new_email = ev.currentTarget.value;
        ev.currentTarget.attributes['em'].nodeValue = ev.currentTarget.value;
        window.operator.ChangeUsersList({[key]:dep}, ()=>{
          user.email = user.new_email;
        });   
    }

    function OnDescChange(ev){  
        let em =  ev.currentTarget.attributes['em'].nodeValue; 
        let user = _.find(dep.staff,{'email':em});
        user.desc = ev.currentTarget.value;
        window.operator.ChangeUsersList({[key]:dep});      
    }

    function OnRemoveOper(ev){

      let em =  ev.currentTarget.attributes['em'].value;      
      let user = _.find(dep.staff,{'email':em})
      if(confirm('Delete '+ user.name.first+' '+user.name.last+"?")){
        var evens = _.remove(dep.staff, function(n) {
            return n.email == em;
        });
        window.operator.ChangeUsersList({[key]:dep});  
        update();
      } 
    }


  </script>